import { trpc } from "@/lib/trpc";
import { UNAUTHED_ERR_MSG } from "@shared/const";
import { QueryClient, QueryClientProvider } from "@tanstack/react-query";
import { httpBatchLink, TRPCClientError } from "@trpc/client";
import * as Sentry from "@sentry/react";
import { createRoot } from "react-dom/client";
import superjson from "superjson";
import App from "./App";
import { getLoginUrl } from "./const";
import "./index.css";
import "./lib/i18n";

// Register Service Worker for PWA support
if ("serviceWorker" in navigator) {
  window.addEventListener("load", () => {
    navigator.serviceWorker
      .register("/sw.js")
      .catch(err => console.warn("SW registration failed", err));
  });
}

// Initialize Sentry for error tracking
if (import.meta.env.VITE_SENTRY_DSN) {
  Sentry.init({
    dsn: import.meta.env.VITE_SENTRY_DSN,
    environment: import.meta.env.MODE,
    integrations: [
      Sentry.browserTracingIntegration(),
      Sentry.replayIntegration({
        maskAllText: true,
        blockAllMedia: true,
      }),
    ],
    // Performance Monitoring
    tracesSampleRate: import.meta.env.MODE === "production" ? 0.1 : 1.0,
    // Session Replay
    replaysSessionSampleRate: 0.1,
    replaysOnErrorSampleRate: 1.0,
    // Additional options
    beforeSend(event) {
      // Don't send errors in development unless explicitly enabled
      if (
        import.meta.env.MODE === "development" &&
        !import.meta.env.VITE_SENTRY_DEBUG
      ) {
        return null;
      }
      return event;
    },
  });
}

//   
const defaultLang = localStorage.getItem("i18nextLng") || "ar";
document.documentElement.dir = defaultLang === "ar" ? "rtl" : "ltr";
document.documentElement.lang = defaultLang;

// Initialize analytics if configured
if (
  import.meta.env.VITE_ANALYTICS_ENDPOINT &&
  import.meta.env.VITE_ANALYTICS_WEBSITE_ID
) {
  try {
    const script = document.createElement("script");
    script.defer = true;
    script.src = import.meta.env.VITE_ANALYTICS_ENDPOINT + "/umami";
    script.dataset.websiteId = import.meta.env.VITE_ANALYTICS_WEBSITE_ID;
    document.head.appendChild(script);
  } catch (error) {
    console.warn("Failed to initialize analytics:", error);
  }
}

const queryClient = new QueryClient();

const redirectToLoginIfUnauthorized = (error: unknown) => {
  if (!(error instanceof TRPCClientError)) return;
  if (typeof window === "undefined") return;

  const isUnauthorized = error.message === UNAUTHED_ERR_MSG;

  if (!isUnauthorized) return;

  try {
    window.location.href = getLoginUrl();
  } catch (err) {
    console.error("Failed to redirect to login:", err);
    // Fallback to login page
    window.location.href = "/login";
  }
};

queryClient.getQueryCache().subscribe(event => {
  if (event.type === "updated" && event.action.type === "error") {
    const error = event.query.state.error;
    redirectToLoginIfUnauthorized(error);
    console.error("[API Query Error]", error);
  }
});

queryClient.getMutationCache().subscribe(event => {
  if (event.type === "updated" && event.action.type === "error") {
    const error = event.mutation.state.error;
    redirectToLoginIfUnauthorized(error);
    console.error("[API Mutation Error]", error);
  }
});

const trpcClient = trpc.createClient({
  links: [
    httpBatchLink({
      url: "/api/trpc",
      transformer: superjson,
      fetch(input, init) {
        return globalThis.fetch(input, {
          ...(init ?? {}),
          credentials: "include",
        });
      },
    }),
  ],
});

createRoot(document.getElementById("root")!).render(
  <trpc.Provider client={trpcClient} queryClient={queryClient}>
    <QueryClientProvider client={queryClient}>
      <App />
    </QueryClientProvider>
  </trpc.Provider>
);
